.view-user {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: -0.2rem;
  width: 100%;
}
.view-user.teacher .nickname {
  border: 0;
  text-align: center;
}
.view-user .avatar {
  width: 0.84rem;
  height: 0.84rem;
  border-radius: 50%;
  overflow: hidden;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}
.view-user .avatar input {
  width: 100%;
  height: 100%;
  opacity: 0;
}
.view-user .nickname {
  margin-top: 0.14rem;
  margin-bottom: 0.36rem;
  width: 1.7rem;
  height: 0.38rem;
  border: 1px solid #b8b8b8;
  border-radius: 0.02rem;
  padding-left: 0.1rem;
  font-size: 0.2rem;
  color: #333333;
  display: flex;
  align-items: center;
}
.view-user .nickname .edit-btn {
  width: 0.38rem;
  height: 0.38rem;
  background: url(./54d9aa38b3f8b9e41e113b962f26f3f5.png) no-repeat center center;
  background-size: 0.19rem auto;
}
.view-user .nickname .text {
  flex: 1;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.view-user .nickname input {
  width: 1.54rem;
  border: 0;
  height: 0.3rem;
  line-height: 0.3rem;
  color: #333333;
  font-size: 0.2rem;
  outline: none;
}
.view-user .logout-btn {
  position: absolute;
  font-size: 0.16rem;
  color: #333333;
  left: 0.28rem;
  bottom: 0.18rem;
  text-decoration: none;
}
.view-user .logout-btn:active {
  opacity: 0.8;
}
.view-user .version {
  font-size: 0.15rem;
  color: #cacaca;
  position: absolute;
  right: 1rem;
  bottom: 0.18rem;
}
.view-user .cleardata {
  font-size: 0.15rem;
  color: #cacaca;
  position: absolute;
  right: 0.28rem;
  bottom: 0.18rem;
  text-decoration: underline;
  cursor: pointer;
}
