.camp-panel {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: -0.4rem;
  margin-bottom: -0.15rem;
  width: 100%;
}
.camp-panel .lesson-box {
  position: relative;
  width: 100%;
  height: 1.57rem;
  background: #fff;
  display: flex;
  align-items: center;
}
.camp-panel .lesson-box .cover {
  width: 1.94rem;
  height: 1.45rem;
  margin-right: 0.2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
.camp-panel .lesson-box .cover img {
  width: 100%;
  height: 100%;
  border-radius: 0.1rem;
}
.camp-panel .lesson-box .info {
  display: flex;
  width: 4.3rem;
  margin-right: 0.2rem;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
}
.camp-panel .lesson-box .info .name {
  color: #333333;
  font-weight: bold;
  display: flex;
  align-items: flex-end;
  height: 0.26rem;
}
.camp-panel .lesson-box .info .name span {
  font-size: 0.24rem;
  height: 0.24rem;
  line-height: 0.24rem;
}
.camp-panel .lesson-box .info .desc {
  width: 100%;
  height: 0.7rem;
  color: #969696;
  font-size: 0.18rem;
  margin-top: 0.1rem;
  margin-bottom: 0.07rem;
  display: flex;
  align-items: flex-start;
  line-height: 0.28rem;
  vertical-align: top;
  overflow: scroll;
}
.camp-panel .lesson-box .info .desc::-webkit-scrollbar {
  display: none;
}
.camp-panel .lesson-box .info .desc span {
  height: 0.26rem;
  line-height: 0.26rem;
  vertical-align: top;
}
.camp-panel .lesson-box .info .tag {
  width: 100%;
  height: 0.26rem;
  display: flex;
  justify-content: flex-start;
}
.camp-panel .lesson-box .info .tag .date-label {
  color: #fff;
  background: #fe6969;
  height: 0.26rem;
  padding-left: 0.1rem;
  padding-right: 0.1rem;
  line-height: 0.26rem;
  text-align: center;
  font-size: 0.18rem;
  border-radius: 0.2rem;
  margin-right: 0.12rem;
}
.camp-panel .lesson-box .info .tag .date {
  width: 70%;
  height: 0.2rem;
  line-height: 0.2rem;
  margin-top: 0.03rem;
  font-size: 0.2rem;
  color: #ff2d2d;
}
.camp-panel .btns-panel {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  justify-content: space-around;
  width: 1.29rem;
}
.camp-panel .btns-panel .start-btn {
  width: 2.2rem;
  height: 0.38rem;
  background: #ff696a;
  color: #fff;
  font-size: 0.2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 0.06rem;
  margin-top: 0.3rem;
}
.camp-panel .btns-panel .start-btn::after {
  content: "";
}
.camp-panel .btns-panel .start-btn[disabled] {
  background: #b5b5b5;
}
.camp-panel .btns-panel .start-btn[disabled]::after {
  content: "\5DF2\4E0A\8BFE";
}
.camp-panel .btns-panel .start-btn.waiting::after {
  content: "\4E0A\8BFE\65F6\95F4\672A\5230";
}
.camp-panel .btns-panel .record-btn {
  background: #8b460e;
}
.camp-panel .btns-panel .record-btn::after {
  content: "\8BFE\7A0B\56DE\653E";
}
.camp-panel .btns-panel .download-btn {
  background: #3882c2;
}
.camp-panel .btns-panel .download-btn::after {
  content: "\4E0B\8F7D\8BFE\7A0B\5305";
}
.camp-panel .btns-panel .waiting-btn {
  background: #b4b4b4;
}
.camp-panel .btns-panel .waiting-btn::after {
  content: "\7B49\5F85\4E0A\8BFE";
}
