.helper-block {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.helper-block .code {
  margin-top: -0.2rem;
  width: 2.2rem;
  height: 2.2rem;
  margin-bottom: 0.1rem;
}
.helper-block .code img {
  width: 100%;
  height: 100%;
}
.helper-block .name {
  width: 2.1rem;
  height: 0.48rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #ff9d1f;
  font-size: 0.2rem;
  color: #ffffff;
  margin-bottom: 0.4rem;
}
.helper-block .tips {
  font-size: 0.18rem;
  color: #b8b8b8;
  margin-bottom: -0.2rem;
}
