.sidebar {
  width: 1.05rem;
  height: 5.09rem;
  position: fixed;
  left: 0.15rem;
  top: 50%;
  margin-top: -2.545rem;
  background: #fff;
  border-top-right-radius: 0.2rem;
  border-bottom-right-radius: 0.2rem;
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.sidebar .center {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.sidebar.teacher {
  height: 2.9rem;
  margin-top: -1.45rem;
}
.sidebar.teacher .device-area {
  border-bottom: 0;
}
.sidebar .avatar-area {
  width: 0.78rem;
  height: 1.48rem;
  border-bottom: 1px solid #dddddd;
}
.sidebar .avatar-area:active {
  opacity: 0.6;
}
.sidebar .avatar-area .avatar {
  width: 0.62rem;
  height: 0.62rem;
  border-radius: 50%;
  overflow: hidden;
  background-repeat: no-repeat;
  background-size: cover;
}
.sidebar .avatar-area .nickname {
  font-size: 0.16rem;
  color: #363636;
  margin-top: 0.1rem;
  width: 0.78rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: center;
}
.sidebar .txt {
  font-size: 0.16rem;
  color: #959bb5;
  margin-top: 0.1rem;
  text-align: center;
}
.sidebar .mycourses-area {
  width: 0.78rem;
  height: 1.14rem;
  border-bottom: 1px solid #dddddd;
}
.sidebar .mycourses-area .mycourses-btn {
  width: 0.42rem;
  height: 0.31rem;
  background: url(./6594837128317fde75a461bb2e88949c.png) no-repeat center center;
  background-size: 100% 100%;
}
.sidebar .mycourses-area:active {
  opacity: 0.6;
}
.sidebar .device-area {
  width: 0.78rem;
  height: 1.14rem;
  border-bottom: 1px solid #dddddd;
}
.sidebar .device-area .device-btn {
  width: 0.39rem;
  height: 0.34rem;
  background: url(./d228416cca98321071830dbef615516b.png) no-repeat center center;
  background-size: 100% 100%;
}
.sidebar .device-area:active {
  opacity: 0.6;
}
.sidebar .help-area {
  flex: 1;
}
.sidebar .help-area .help-btn {
  width: 0.36rem;
  height: 0.36rem;
  background: url(./944fa3bc3ac63ebac35ea6e87b1834e9.png) no-repeat center center;
  background-size: 100% 100%;
}
.sidebar .help-area:active {
  opacity: 0.6;
}
.sidebar.flow {
  top: calc(50% - 0.7rem);
}
.camp-entry {
  width: 1.05rem;
  height: 1.2rem;
  background: #fff;
  position: fixed;
  bottom: 0.5rem;
  left: 0.15rem;
  border-top-right-radius: 0.2rem;
  border-bottom-right-radius: 0.2rem;
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.camp-entry .camp-area {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex: 1;
}
.camp-entry .camp-area .icon {
  width: 0.39rem;
  height: 0.36rem;
  background: url(./a6a9691c823ee8b808626f80c95d23c7.png) no-repeat center center;
  background-size: 100% 100%;
}
.camp-entry .camp-area .txt {
  font-size: 0.16rem;
  color: #959bb5;
  margin-top: 0.1rem;
  text-align: center;
}
